/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  overflow-x: hidden;
  /* Защита от горизонтальной прокрутки */
}

h1,
h2,
h3 {
  font-weight: bold;
  margin-bottom: 15px;
}

/* Article Section */
.article-section {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  /* Начальная невидимость */
  transform: translateY(50px);
  /* Начальное положение снизу */
  animation: slideUp 1s ease-out forwards;
  /* Добавляем анимацию */
}

.article-section h2 {
  font-size: 24px;
  color: #2c3e50;
}

.article-section h3 {
  font-size: 20px;
  margin-top: 20px;
  color: #2980b9;
}

.article-section p {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 15px;
  color: #7f8c8d;
}

.article-section i {
  font-style: italic;
  font-size: 14px;
  color: #2c3e50;
  display: block;
  margin-top: 20px;
}

/* Слайд снизу */
@keyframes slideUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Calorie Tracker */
.calorie-tracker {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  /* Начальная невидимость */
  transform: translateY(50px);
  /* Начальное положение снизу */
  animation: slideUp 1s ease-out 0.3s forwards;
  /* Добавляем анимацию */
}

.calorie-tracker h2 {
  font-size: 24px;
  color: #2c3e50;
  margin-bottom: 20px;
}

.calorie-tracker input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.calorie-tracker button {
  width: 100%;
  padding: 10px;
  background-color: #2980b9;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

.calorie-tracker button:hover {
  background-color: #3498db;
}

.calorie-tracker div {
  margin-top: 20px;
  padding: 10px;
  background-color: #ecf0f1;
  border-radius: 8px;
}

/* Workout Plan */
.workout-plan {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  /* Начальная невидимость */
  transform: translateY(50px);
  /* Начальное положение снизу */
  animation: slideUp 1s ease-out 0.5s forwards;
  /* Добавляем анимацию */
}

.workout-plan h2 {
  font-size: 24px;
  color: #2c3e50;
}

.workout-plan input {
  width: calc(33.333% - 10px);
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  margin-right: 10px;
  transition: border-color 0.3s ease;
}

.workout-plan button {
  padding: 10px;
  background-color: #2980b9;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

.workout-plan button:hover {
  background-color: #3498db;
}

.workout-plan ul {
  list-style-type: none;
  padding: 0;
}

.workout-plan li {
  background-color: #ecf0f1;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  /* Начальная невидимость */
  transform: translateY(50px);
  /* Начальное положение снизу */
  animation: slideUp 1s ease-out 0.7s forwards;
  /* Добавляем анимацию */
}

.workout-plan button {
  margin-left: 10px;
}

.workout-plan button:hover {
  background-color: #e74c3c;
}

/* Diary */
.diary {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  /* Начальная невидимость */
  transform: translateY(50px);
  /* Начальное положение снизу */
  animation: slideUp 1s ease-out 0.9s forwards;
  /* Добавляем анимацию */
}

.diary h2 {
  font-size: 24px;
  color: #2c3e50;
}

.diary h3 {
  font-size: 20px;
  margin-top: 20px;
  color: #2980b9;
}

.diary ul {
  list-style-type: none;
  padding: 0;
}

.diary li {
  background-color: #ecf0f1;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.diary button {
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  cursor: pointer;
}

.diary button:hover {
  background-color: #c0392b;
}

.diary input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

/* Buttons and Interactions */
button {
  transition: background-color 0.3s ease;
}

/* Layout Adjustments for Small Screens */
@media (max-width: 768px) {

  /* Calorie Tracker */
  .calorie-tracker input,
  .workout-plan input,
  .workout-plan button,
  .diary input {
    width: 100%;
    margin-right: 0;
  }

  /* Workout Plan Inputs */
  .workout-plan input {
    width: 100%;
    margin-right: 0;
  }

  /* Adjust button widths */
  .calorie-tracker button,
  .workout-plan button,
  .diary button {
    width: 100%;
  }

  /* Flex layout for workout plan items */
  .workout-plan li {
    flex-direction: column;
    align-items: flex-start;
  }
}
